<template>
<div>
  <home @msg-modal="showImage"/>
  <d-modal @close="closeModal" :info="info" :visible="show"/>
</div>
</template>
<script>
import Home from './Home'
import DModal from '../../Shared/Components/DModal'
export default {
  name: 'main-home',
  components: {
    Home,
    DModal
  },
  data () {
    return {
      info: {},
      show: false
    }
  },
  methods: {
    showImage (value) {
      this.info = value
      this.show = true
    },
    closeModal () {
      this.show = false
    }
  }
}
</script>



