import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/app/Home/Components/Main'
import Contato from '@/app/Contact/Components/Contact'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Main
    },
    {
      path: '/contato',
      name: 'Contato',
      component: Contato
    }
  ]
})



