<template>
    <v-layout row>
      <v-flex xs12 offset>
        <v-card>
          <v-container fluid grid-list-lg>
            <v-layout row wrap>
              <v-flex xs12 sm6 md3
                  v-for="(item, i) in items"
                  :key="i"
                >
                <v-card>
                  <v-card-media
                    class="white--text"
                    height="200px"
                    :src="item.imageUrl"
                  >
                  </v-card-media>
                  <v-card-title>
                    <div>
                      <span class="grey--text">Code: {{item.id}}</span><br>
                      <span>{{item.description}}</span>
                    </div>
                  </v-card-title>
                  <v-card-actions>
                    <div>
                      <span>Preço: {{item.price}}</span>
                      <v-btn flat small left color="purple" @click.prevent="openMyDialog(item)">Abrir</v-btn>
                    </div>
                  </v-card-actions>
                </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
</template>
<script>
  export default {
    name: 'home',
    data: () => ({
      show: false,
      openModal: false,
      items: []
    }),
    mounted () {
      this.fetch()
    },
    methods: {
      openMyDialog (value) {
        this.$emit('msg-modal', value)
      },
      fetch () {
        fetch('/static/data/data.json')
          .then(response => response.json())
          .then(data => {
            this.items = data
          })
      }
    }
  }
</script>



